<template>
  <div class="box">
    <el-form size="medium" label-width="92px">
      <el-form-item label="*">
        <img :src="industry.industryLogo">
      </el-form-item>
      <div class="dashed"></div>
      <el-form-item label="*">
        <el-input v-model="industry.industryName"></el-input>
      </el-form-item>

      <el-form-item label="*">
        <el-input v-model="industry.industryPhone"></el-input>
      </el-form-item>

      <el-form-item label="*">
        <el-cascader @change="addressChange" v-model="testAddress" :options="city" :props="cascaderCityConf" style="width: 180px;" change-on-select></el-cascader>
        <el-input v-model="industry.industryStreet" style="width: 200px;" class="editState"></el-input>

      </el-form-item>

      <el-form-item label="*">
        <quill-editor v-model="industry.industryDesc" style="height: 190px" ref="myQuillEditor" :options="editorOption" @change="onEditorChange($event)">
        </quill-editor>
      </el-form-item>

      <el-form-item label="" style="margin-top: 86px">
        <img :src="industry.industryUrl" width="90" height="90" style="float: left;">
        <div style="float: left;margin:10px 0 0 20px;">
          <el-button></el-button>
          <p class="font-small mar-t-15">300*300.jpg/.png/.gif,1M</p>
        </div>
      </el-form-item>

      <el-form-item label="" style="padding: 15px;">
        <div class="other mar-t-15" v-for="(item,index) in activityShopList" :key="'activityShopList' + index">
          <div class="other-head" @click="showDetails(index)">
            <el-checkbox label="" v-model="item.confirm"></el-checkbox>
            <span class="mar-10" v-show="!item.shopShow"></span>
            <el-input size="mini" v-model="item.shopName" v-show="item.shopShow" style="width: 180px"></el-input>
            <div class="remove" @click="removeShop(index)">x</div>
            <img src="" width="15" height="15">
          </div>

          <div class="portais" v-show="item.shopShow">
            <div class="portais-text">
              <p class="font-small"></p>
              <p class="font-small"></p>
              <p class="font-small"></p>
            </div>
            <div class="portais-input">
              <el-input size="mini" v-model="item.shopPhone"></el-input>
              <el-input size="mini" v-model="item.contacts"></el-input>
              <el-input size="mini" v-model="item.shopAddress"></el-input>
            </div>
            <el-button @click="doPortai(index)" size="mini" style="float: right;margin:8px 24px 10px 0px"></el-button>
            <div style="clear: both;"></div>
          </div>
        </div>
        <el-button size="small" @click="addShop"></el-button>
      </el-form-item>
    </el-form>

    <div class="next">
      <router-link to="/nav-new/basic-setting/share">
        <img src="static/imgs/manage/editor/next.png">
        <span></span>
      </router-link>
    </div>
  </div>
</template>

<script type="text/javascript">
import CITY from 'src/lib/city.js'
export default {
  props: {
    industry: {
      type: Object
    },
    activityShopList: {
      type: Array
    }
  },
  data () {
    return {
      name: '',
      portaisShow: true,
      editorOption: {
        // 
        modules: {
          toolbar: [
            [
              'bold',
              'blockquote',
              'italic',
              'underline',
              'strike',
              'indent',
              'image'
            ]
          ]
        }
      },
      city: CITY, // js
      cascaderCityConf: {
        // 
        value: 'name',
        label: 'name',
        children: 'sub'
      },
      testAddress: []
    }
  },
  methods: {
    // 
    showDetails (index) {
      let item = this.activityShopList[index]
      item.shopShow = true
      this.$set(this.activityShopList, index, item)
    },
    addressChange () {
      console.log(this.testAddress)
    },
    onEditorChange ({ quill, html, text }) {
      this.$emit('sponsorChange', html)
    },
    // 
    doPortai (index) {
      let item = this.activityShopList[index]
      item.shopShow = false
      this.$set(this.activityShopList, index, item)
    },
    // 
    addShop () {
      this.activityShopList.push({
        activityId: '',
        contacts: 'ss',
        shopName: 'ddd',
        id: '',
        shopAddress: '',
        shopPhone: ''
      })
    },
    // 
    removeShop (index) {
      this.activityShopList.splice(index, 1)
    }
  },
  created () {
    this.activityShopList.forEach(function (item) {
      item.shopShow = false
      item.confirm = true
    })
  },
  mounted () {
    console.log(this.industry)
    this.testAddress.push(this.industry.industryProvince)
    this.testAddress.push(this.industry.industryCity)
    this.testAddress.push(this.industry.industryArea)
  }
}
</script>

<style type="text/css" src="common/style/module/manage/new-editor.scss" scoped></style>
<style type="text/css">
.box .el-form-item__content {
  border: 0 !important;
  border-bottom: 0 !important;
}
</style>
<style lang="scss" scoped>
.w-220 {
  width: 220px;
}

.portais {
  padding: 10px;
  .portais-text {
    width: 80px;
    float: left;
    margin-top: 6px;
    p {
      margin: 0 0 27px 0;
    }
  }

  .portais-input {
    width: 200px;
    float: left;
  }
}
.other {
  border: 1px solid #d9d9d9;
  padding: 0 15px;
  overflow: hidden;
  .other-head {
    position: relative;

    .remove {
      position: absolute;
      top: 0;
      right: 0;
      width: 15px;
      height: 15px;
      font-size: 20px;
      cursor: pointer;
      color: #fc4a46;
    }

    img {
      float: right;
    }
  }
}
</style>


